.app {
  position: relative;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;

  font-family: 'Montserrat', sans-serif;

  background: url('/src/img/mainBg.jpg') no-repeat center;
  
}
.filter-wrap {
  backdrop-filter: blur(3px) brightness(0.6);
  
}

.loader {
  position: relative;

  width: 50%;
  height: 30px;
  margin-bottom: 10px;

  border-radius: 10px;
}

.loader-item {
  position: absolute;
  
  width:30px;
  height: 30px;

  border-radius: 50%;
}

.item-first {
  top: calc(50% - 15px);
  left: 0;
  background: url('../../img/pizza-loader.svg') no-repeat center;
  animation: loader 1.5s ease-in-out infinite;
}

.item-second {
  top: calc(50% - 15px);
  right: 0;
  background: url('../../img/pizza-loader2.svg') no-repeat center;
  animation: loader-revers 1.5s ease-in-out infinite;
}

@keyframes loader {
  0% {left: 0;}
  25% {transform: scale(0.7); z-index: 100;}
  50% {left: calc(100% - 30px); transform: scale(1);}
  75% {transform: scale(1.3); z-index: 200;}   
  100% {left: 0; transform: scaleZ(1);}
}

@keyframes loader-revers {
  0% {right: 0;}
  25% {transform: scale(1.3); z-index: 200;}
  50% {right: calc(100% - 30px); transform: scale(1);}
  75% {transform: scale(0.7); z-index: 100;}   
  100% {right: 0; transform: scaleZ(1);}
 }

 .loader-placeholder {
  width: 50%;
  height: 30px;
  margin-bottom: 10px;
 }

.btn {
  height: 40px;
  width: 70px;

  font-size: 14px;
  color: rgb(156, 145, 145);
  background: none;
  border: 3px solid rgba( 255, 255, 255, 0 );
  padding: 0;
  outline: none;

  font-family: 'Montserrat', sans-serif;
  
  transition: all 0.3s ease;
  box-sizing: content-box;
}

.btn:hover {
  cursor: pointer;
  border-bottom: 3px solid #F48E0E;
}

.btn--active {
  border: 3px solid #F48E0E;
  border-radius: 15px;
}
.btn--active:hover {
  background-color: #F48E0E;
}

 .copy-btn {
   border-bottom: 1px solid rgba( 255, 255, 255, 0.18 );
   transition: border 0.3s ease;
 }
 
 .copy-btn:hover {
   cursor: pointer;
   border-bottom: 1px solid #F48E0E ;
 }

 .warning-text {
    height: 40px;
    text-align: center;
    font-weight: 600;
    color: red;
    opacity: 1;
    transition: all 0.7s ease;
  }
  
  .warning-text-hidden {
    height: 40px;
    text-align: center;
    opacity: 0;
    transition: all 0.7s ease;
    
  }