.nav {
    
    max-width: 1440px;
    height: 60px;
    margin: 0 auto;
    
}

.nav__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  
   
    
    margin: 0 2%;

    
}

.nav__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__icon {
    width: 50px;
    height: 50px;
    background: url('../../img/pizza.svg') no-repeat center;
}

.nav__title-text {
    margin: 0;

    font-family: 'Pacifico', cursive;
    font-weight: 300;
    font-size: 18px;
    color: rgb(255, 255, 255);
}

.nav__userbar {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 10px;  
    
}

.nav__user-data {
    display: grid;
    place-items: center;
    height: 50px;

    font-size: 14px;
}

.nav__avatar {
    display: grid;
    place-items: center;

    width: 40px;
    height: 50px;
    border: none;

    border-bottom: 3px solid rgba( 255, 255, 255, 0 );
    transition: all 0.3s ease;
}

.nav__avatar:hover {
    cursor: pointer;
    border-bottom: 3px solid #F48E0E;
}

.nav__img {
    width: 35px;
    height: 35px;
    
}

.avatar-guest {
    background: url('../../img/guest.svg') no-repeat center;
    
}

.nav__username {
    margin: 0;
    color: #fff
}