  .pizza__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    width: 100%;
    padding:50px 0;
    
    transition: all 0.5s ease;  
  }

  .pizza__title {
    color: white;
  }
  
  .pizza__body {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    padding: 10px 5%;
    
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 3.0px );
    -webkit-backdrop-filter: blur( 3.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
  
    transition: opacity 0.3s ease;
  }
  
  .pizza__img {
    position: relative;
    width: 250px;
    height: 250px;
  
    border-radius: 50%;
    border: 10px solid #F48E0E;
    background-color: #FEC746;
  }

  .pizza__editor {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 300px;
    min-height: 100px;
    padding-left: 0;
  }
  
  .editor__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 40px;
  
    list-style-type: none;
    font-family: 'Montserrat', sans-serif;
  
    background: rgba( 255, 255, 255, 0.05 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 6.5px );
    -webkit-backdrop-filter: blur( 6.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    transition: background-color 0.3s ease;
  }

  .editor__item:hover {
    background-color: rgba(255, 255, 255, 0.3);
    cursor: pointer;
  }

  .editor__text {
    margin-left: 15px;
    font-weight: 600;
    color: white;
  }
  
  .editor__indicator {
    width: 30px;
    height: 30px;
    background: none;
    border: none;
    border-radius: 8px;
  
    margin-right: 20px;
  
    background: rgba( 103, 224, 224, 0.30 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 20.0px );
    -webkit-backdrop-filter: blur( 20.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
  }
  
  .editor__indicator--false {
    background: url('../../img/close.svg') no-repeat center;
  }

  .editor__indicator--true {
    background: url('../../img/check-mark.svg') no-repeat center;
  }

   .editor__item-disabled {
    background: rgba( 255, 8, 8, 0.30 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7.0px );
    -webkit-backdrop-filter: blur( 7.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    opacity: 0.6;
  }

  .editor__item-disabled:hover {
    background: rgba( 255, 8, 8, 0.70 );
  }
  
  .editor__item-active {
    background: rgba(66, 255, 8, 0.3);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 7.0px );
    -webkit-backdrop-filter: blur( 7.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
  }

  .editor__item-active:hover {
    background: rgba(66, 255, 8, 0.5);
  }
  
  .ingredient{
    position: absolute;
    top: 0;
    left: 0;
  
    width: 250px;
    height: 250px;
    border-radius: 50%;
  }
  
  .cheese {
    background: url('../../img/cheese.svg'); 
  }

  .pepperoni {
    background: url('../../img/pepperoni.svg');
  }

  .jalapeno {
    background: url('../../img/jalapeno.svg');
  }

  .pineapple {
    background: url('../../img/pineapple.svg');
  }

  .onion {
    background: url('../../img/onion.svg');
  }

  .cucumber {
    background: url('../../img/cucumber.svg');
    background-position: 10% 10%;
  }

  .bacon {
    background: url('../../img/bacon.svg');
    background-position: 5% 7%;
  }
  
  .button-disabled {
    opacity: 0.3;
  }
  
  .text-disable {
    opacity: 0.3;
  }
 