.modal-wrap {
    position: absolute;

    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    background-color: rgba(0,0,0, 0.5);
    transition: all 0.5s ease;
}

.visible {
   
    visibility: visible;
    opacity: 1; 
    
}

.hidden {
    visibility: hidden;  
    opacity: 0;
}

.modal__login {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    width: 25%;
    min-width: 300px;
    padding: 5px;

    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 3.0px );
    -webkit-backdrop-filter: blur( 3.0px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.modal__guest {
    width: 50%;
    height: 100px;
    margin: 20px 0;
    background: url('../../img/guest-modal.svg') no-repeat center;
}

.modal__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
}

.modal__input {
    width: 100%;
    height: 20px;
    margin-bottom: 20px;
    padding: 5px;
    
    border: none;
    background: none;
    outline: 0;
    border: 2px solid rgba( 255, 255, 255, 0.18 );
    border-radius: 10px;
    font-size: 16px;
    color: gainsboro;

    transition: border-color 0.7s ease;
}

.modal__input:focus {
    border-color: gainsboro;
}
.modal__btn {
    width: 80%;
    border: 3px solid rgba( 255, 255, 255, 0.18 );
    border-radius: 10px;
    margin-bottom: 10px;
}

.modal__help {
    margin: 0;
    text-align: center;
    color: rgba( 255, 255, 255, 0.18 );
}

.error {
    border-color: red;
}